<script>
// 官网首页导航
// throttle | debounce
import { throttle } from 'throttle-debounce';

export default {
  name: "Nav",
  data () {
    return {
      navVisible: false,
      isWhiteBg: false,
      canBack:true,
      bossUrl:'https://www.zhipin.com/gongsi/2a7968f8f920b6f133R-3tm7GQ~~.html?ka=company-intro'
    }
  },
  watch:{
    $route(to){
      this.canBack = to.name == 'home' ? false : true;
    }
  },
  methods:{
    joinUs(){
      window.open(this.bossUrl,'_blank')
      this.close()
    },
    close(){
      this.navVisible= false;
    },
    scroll:throttle(200,function(){
      // let scrollTop = event.target.scrollTop; 
      let scrollTop = (document.documentElement && document.documentElement.scrollTop) || 
              document.body.scrollTop;
      this.isWhiteBg = scrollTop>=40?true:false;
    }),
    back(){
      this.$router.go(-1)
    }
  },
  mounted(){
    // document.addEventListener('scroll',this.scroll)
    window.onscroll = this.scroll;
  },
  beforeDestroy(){
    // document.removeEventListener('scroll',this.scroll)
    window.onscroll = null;
  }
};
</script>

